body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.styles_statusBar__2DNnl {
  display: flex;
  width: 100%;
  /* height: 42px; */
  border-bottom: #29487d;
  background-color: #4267b2;
  margin-bottom: 20px;
  padding: 5px 0;
}
.styles_statusBar__2DNnl button {
   display: flex;
   background-color: transparent;
   border: 2px solid var(--paletteColor5);
   margin: auto;
   border-radius: 10px;
   padding: 5px 25px;
}
.styles_statusBar__2DNnl img {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border: 1px solid var(--paletteColor5);
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
}
.styles_statusBar__2DNnl span {
    align-self: center;
    color: white;
}
.styles_composer__1FwBx {
    display: flex;
    position: relative;
    width: 500px;
    height: 160px;
    margin-bottom: 10px;
    padding: 12px;
    border: 1px solid var(--paletteColor4);
    border-radius: 3px;
    background-color: var(--paletteColor2);
}
.styles_composer__1FwBx img {
   width: 40px;
   height: 40px;
   margin-right: 15px;
   -webkit-transition: border .5s ease;
   transition: border .5s ease;
   border: 1px solid var(--paletteColor5);
   border-radius: 50%;
   cursor: pointer;
}

.styles_composer__1FwBx input[type='submit'] {
   position: absolute;
   right: 10px;
   bottom: 10px;
   width: 50px;
   height: 22px;
   -webkit-transition: background-color .2s ease-in;
   transition: background-color .2s ease-in;
   border: 0;
   border-radius: 2px;
   outline: none;
   background-color: var(--paletteColor3);
   color: var(--paletteColor2);
   letter-spacing: 1px;
   cursor: pointer;
}
.styles_composer__1FwBx input[type='submit']:hover {
    background-color: var(--paletteColor7);
}
.styles_composer__1FwBx textarea { 
   width: 410px;
   height: 80px;
   margin-top: 7px;
   -webkit-transition: font-size .5s ease;
   transition: font-size .5s ease;
   border: 0;
   outline: 0;
   font-size: 14px;
   font-weight: 300;
   resize: none;
}
.styles_composer__1FwBx textarea::scrollbar {
    display: none;
}
.styles_composer__1FwBx textarea::-webkit-input-placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea::-ms-input-placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea::placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea:focus::-webkit-input-placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus::-ms-input-placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus::placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus {
    font-size: 24px;
}
.styles_post__10vqr {
  position: relative;
  width: 500px;
  margin-top: 10px;
  padding: 12px;
  border: 1px solid var(--paletteColor3);
  border-radius: 3px;
  background-color: var(--paletteColor2);
  word-wrap: break-word;
}
.styles_post__10vqr img {
    width: 40px;
    height: 40px;
    margin-right: 15px;
    border: 1px solid var(--paletteColor5);
    border-radius: 50%;
    cursor: pointer;
}
.styles_post__10vqr a,
.styles_post__10vqr time {
   position: absolute;
   left: 60px;
   text-decoration: none;
   cursor: pointer;
}
.styles_post__10vqr time:hover,
.styles_post__10vqr a:hover {
   text-decoration: underline;
}
.styles_post__10vqr a{
    top: 15px;
    color: var(--paletteColor7);
    font-weight: 700;
}
.styles_post__10vqr time {
   top: 35px;
   color: var(--paletteColor5);
   font-size: 12px;
}
.styles_post__10vqr p {
   margin-top: 16px;
   font-size: 14px;
}
.styles_feed__2RQ-o {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--paletteColor3);
  min-height: 100vh;
}
:root {
    --paletteColor1: #000;
    --paletteColor2: #FFF;
    --paletteColor3: #E9EBEE;
    --paletteColor4: #808080;
    --paletteColor5: #90949C;
    --paletteColor6: #4267B2;
    --paletteColor7: #365899;
    --paletteColor8: #282828;
    --paletteColor9: #616770;
    --paletteColor10: #E1E2E3;
    --paletteColor11: #F6F7F9;
    --paletteColor12: #DDD;
    --paletteColor13: #F25A41;
}

